.headerShopBox {
  background: url('../../../img/shop/rust\ 1.jpg') lightgray 50%;
  height: 315px;
  width: 100%;
}
.filterHeader {
  position: absolute;
  top: 90px;
  height: 315px;
  left: 0;
  right: 0;
}
.contentHeaderShopBox {
  position: absolute;
  top: 90px;
  height: 315px;
  left: 0;
  right: 0;
  display: flex;
}
.centerHeaderShopBox {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 56px;
  h1 {
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 22px;
  }
  p {
    font-size: 18px;
    font-weight: 600;
    line-height: normal;
  }
}
.dopInfoHeaderShopBox {
  margin-top: 32px;
  display: flex;
  cursor: pointer;
  align-items: center;
  p {
    margin-left: 8px;
    color: #8774b8;
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
  }
}
.menuHeaderShopBox {
  display: flex;
}
.sectionMenuHeaderShopBox {
  width: 256px;
  padding-bottom: 17px;
  padding-top: 11px;
  text-align: center;
  cursor: pointer;
}
.activeSectionMenuHeaderShopBox {
  background-color: #171226;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
