.oneColumnCard {
  width: 311px;
  box-sizing: border-box;
  padding-top: 19px;
  padding-bottom: 20px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    position: relative;
    top: -12px;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
  }
  .lightBtn {
    width: 271px;
    padding: 15px 0px 16px 0px;
    margin-top: 10px;
  }
}
.imageOneColumnCard {
  height: 312px;
}
