.btn {
  @include displayFlexAIC;
  justify-content: center;
  border-radius: 12px;
  border: none;
  cursor: pointer;

  svg {
    margin-right: 12px;
  }
}

.blackBtn {
  background-color: transparent;
  border: 2px solid $purple;
  color: $lightPurple;
  transition: 0.2s;

  &:hover {
    border-color: $lightBorderForBlackBtn;
  }
  span {
    color: $lightPurple;
  }
}

.lightBtn {
  background: linear-gradient(90deg, #4046eb 0%, #419fff 100%);
  color: $white;
  transition: 0.2s;

  &:hover {
    background: linear-gradient(90deg, #4046eb 0%, #4046eb 100%);
  }
  span {
    color: $white;
  }
}
