@import '../../variables';
.balance {
  display: flex;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 35px;
  justify-content: space-between;
}

.balanceTitle {
  color: $lightPurple;
  font-size: 16px;
  font-weight: 600;
}

.balanceCount {
  color: $white;
  font-size: 32px;
  font-weight: 700;
}

.balanceBtnBlock {
  display: flex;
  align-items: center;
  margin-left: 40px;
}

.balanceBtn {
  width: 210px;
  height: 50px;
  margin-left: 12px;

  &:first-child {
    margin-left: 0;
  }
}

.tableForDetails {
  td {
    padding: 35px 20px;
  }

  thead th:nth-child(1) {
    width: 18%;
  }

  thead th:nth-child(2) {
    width: 15%;
  }

  thead th:nth-child(3) {
    width: 15%;
  }

  thead th:nth-child(4) {
    width: 17%;
  }

  thead th:nth-child(5) {
    width: 20%;
  }

  thead th:nth-child(6) {
    width: 15%;
  }
}
