@import "../../variables";

.buySilver {
  &Modal {
    width: 482px;
    height: 502px;
    max-width: 100%;

    &Inner {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @media (max-width: 425px) {
        height: calc(100vh - 94px);
      }
    }
  }

  &Text {
    color: $white;
    font-family: Bahnschrift;
    font-size: 16px;
    font-weight: 600;
  }

  @media (max-width: 425px) {
    height: 100%;
  }
}

.selectAPackageBlock {
  margin-top: 32px;

  h4 {
    color: $lightPurple;
    font-family: Bahnschrift;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 12px;
  }
}

.selectAPackageContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4px;
}

.inputSumBlock {
  margin-top: 20px;

  h4 {
    color: $lightPurple;
    font-family: Bahnschrift;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 12px;
  }
}
.inputSum {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &Wrap {
    width: 216px;
    height: 50px;
    position: relative;
    border-radius: 12px;
    background-color: $purple;

    input {
      width: 100%;
      height: 50px;
      border-radius: 12px;
      background-color: $purple;
      padding: 13px 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
    }

    svg {
      position: absolute;
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
    }
  }
}

.silverBonus {
  margin-top: 23px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &Text {
    color: $orange;
    font-family: Bahnschrift;
    font-size: 14px;
    font-weight: 600;

    span {
      color: $white;
    }
  }

  &Count {
    display: flex;
    align-items: center;
  }
}

.buySilverBtn {
  width: 100%;
  height: 50px;
  margin-top: 32px;

  @media (max-width: 425px) {
    align-self: flex-end;
  }
}
