@import '../../mixins';
@import '../../variables';

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(13, 10, 22, 0.6);
  backdrop-filter: blur(6px);
  @include displayFlexAIC;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
}

.modalActive {
  z-index: 10;
  opacity: 1;
  pointer-events: all;

  @media (max-width: 425px) {
    z-index: 10000000;
  }
}

.modalContent {
  overflow: scroll;
  z-index: 10;
  padding: 32px;
  border-radius: 12px;
  background-color: $tableDark;
  transform: scale(0.5);
  transition: 0.4s;
  position: relative;

  &::-webkit-scrollbar {
    display: none;
  }

  &Active {
    transform: scale(1);
  }

  @media (max-width: 425px) {
    border-radius: 0;
    height: 100vh;
    padding: 20px 12px;
  }
}

.modalBackground {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  height: 200px;
  width: 100%;
  background: linear-gradient(180deg, #140f21 0%, #221939 100%);
}

.modalHeader,
.modalHeaderSmallBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;

  &SmallBottom {
    margin-bottom: 20px;
  }

  &Title {
    color: #fff;
    font-family: Bahnschrift;
    font-size: 32px;
    font-weight: 700;

    @media (max-width: 625px) {
      font-size: 28px;
    }
    @media (max-width: 375px) {
      font-size: 22px;
    }
  }

  svg {
    cursor: pointer;
  }
}

.modalTest {
  width: 470px;
  height: fit-content;
}
