.boxScaleWithTitle {
  width: 268px;
}
.headerScale {
  @include displayFlexAIC;
  justify-content: space-between;
  margin-bottom: 9px;
}
p {
  padding: 0;
  margin: 0;
}
.titleScale {
  font-weight: 600;
}
.boxScale {
  background-color: #251d3c;
  height: 24px;
  width: 268px;
  border-radius: 6px;
}
.activeScale {
  @include displayFlexAIC;
  background: -webkit-linear-gradient(left, rgba(64, 70, 235, 1), rgba(65, 159, 255, 1));
  width: 50%;
  height: 100%;
  border-radius: 6px;
  display: flex;
  padding-left: 12px;
}
.labelScale {
  font-weight: 600;
  font-size: 14px;
  text-justify: center;
}
.iconCopy {
  cursor: pointer;
  opacity: 0.9;
  &:hover {
    opacity: 1;
  }
}
