.containerCustomPage {
  //   padding-bottom: 40px;
}
.boxWithSection {
  display: grid;
  grid-template-columns: 0.2fr 0.8fr;
  gap: 60px;
  @media (max-width: $largeScreens) {
    grid-template-columns: 1fr;
    gap: 0px;
  }
}
.boxNoSection {
  width: 100%;
}
.labelSectionCustomPage {
  @include displayFlexAIC;
  flex-direction: row;
  margin-bottom: 20px;
  @media (max-width: $largeScreens) {
    padding-left: 12px;
  }
}
.iconSectionCustomPage {
  margin-right: 12px;
}
.sectionCustomPage {
  margin-bottom: 64px;
  box-sizing: border-box;
  @media (max-width: $largeScreens) {
    margin-bottom: 0px;
    padding-left: 12px;
    padding-right: 12px;
    padding-top: 32px;
    padding-bottom: 32px;
    border-top: 1px solid #8774b84a;
  }
}
