.boxListSections {
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media (max-width: $largeScreens) {
    flex-direction: row;
    gap: 32px;
    width: 100%;
    max-width: 100vw;
    overflow-x: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    padding-left: 12px;
    border-bottom: 1px solid #8774b856;
  }
}
.sectionListSection {
  color: #8774b8;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
  @media (max-width: $largeScreens) {
    padding-bottom: 12px;
  }
}
.sectionListSectionActive {
  color: #fff;
  @media (max-width: $largeScreens) {
    border-bottom: 1px solid #4195fd;
  }
}
