@font-face {
  font-family: Bahnschrift;
  src: url(../fonts/Bahnschrift.ttf);
}

* {
  font-family: Bahnschrift, sans-serif;
  scroll-behavior: smooth;
  scroll-snap-align: center;
  scroll-margin-top: 100px;
}

body {
  background-color: #171226;
  color: $white;
}

body::-webkit-scrollbar {
  width: 5px; /* ширина всей полосы прокрутки */
}

body::-webkit-scrollbar-track {
  background: $lightPurple; /* цвет зоны отслеживания */
}

body::-webkit-scrollbar-thumb {
  background-color: $purple; /* цвет бегунка */
  border-radius: 20px; /* округлось бегунка */
  border: 3px solid $purple; /* отступ вокруг бегунка */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
}

span {
  font-weight: 600;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

// svg {
//   cursor: pointer;
// }

input {
  color: $white;

  &::placeholder {
    color: $lightPurple;
  }
}
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none; // Yeah, yeah everybody write about it
}

input[type='number'],
input[type='number']:hover,
input[type='number']:focus {
  appearance: none;
  -moz-appearance: textfield;
}
.container {
  height: 100%;
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
}
.titlePage {
  color: #fff;
  font-size: 44px;
  font-weight: 700;
  margin-top: 64px;
  margin-bottom: 64px;
  @media (max-width: $largeScreens) {
    margin-top: 32px;
    margin-bottom: 32px;
    color: #fff;
    font-size: 24px;
    margin-left: 12px;
  }
}
.blurBackground {
  position: fixed;
  width: 100%;
  height: 100%;
  filter: blur(5px);
  -webkit-filter: blur(2px);
  backdrop-filter: blur(2px);
  z-index: 111;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.notScroll {
  overflow: hidden;
}
.tableWrap {
  min-width: 100%;
  display: flex;
  overflow: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}
.tableImg {
  width: 60px;
  height: 60px;
}
.tableProductName {
  display: flex;
  align-items: center;
  gap: 12px;
}
.noRecords {
  text-align: center;
  margin: 100px auto 0;
  font-size: 48px;
}
